}

void handleCommand(String cmd) { //æ¥æ”¶åˆ°å­—ç¬¦æŒ‡ä»¤ååˆ†å‘ç»™å¯¹åº”ç¯æ•ˆå‡½æ•°
  if (cmd == "rainbow") {
    rainbowEffect();
  }
  else if (cmd == "warm") {
    warmLight();
  }
  else if (cmd == "cold") {
    coldLight();
  }
  else if (cmd == "close") {
    turnOff();
  }
  else if (cmd == "heart") {
    heartEffect();
  }
  else {
    // å¦‚æœæ”¶åˆ°æœªçŸ¥å‘½ä»¤
    turnOff();
  }
}

// å½©è™¹ç¯
void rainbowEffect() {
  static uint8_t hue = 0; //è‰²è°ƒå€¼ hue æ˜¯ 0~255 çš„ç¯å½¢å€¼ï¼Œä»£è¡¨ HSV è‰²å½©æ¨¡å‹ä¸­çš„è‰²ç›¸è§’åº¦ã€‚

  while (true) {
    // æ£€æŸ¥ mySerial æ˜¯å¦æ”¶åˆ°"æ‰“æ–­"æŒ‡ä»¤
    if (mySerial.available()) {
      String interruptCmd = mySerial.readString();
      interruptCmd.trim();

      if (interruptCmd == "close" || interruptCmd == "warm" || interruptCmd == "cold" || interruptCmd == "heart") {
        handleCommand(interruptCmd);  // æ‰§è¡Œå…¶ä»–æŒ‡ä»¤
        break; // è·³å‡ºå½©è™¹æ•ˆæœï¼Œå›åˆ°ä¸» loop
      }
    }

    for (int i = 0; i < NUM_LEDS; i++) { // æ­£å‘æ‰«æç¯ç ï¼ˆå½©è™¹æ¨è¿›ï¼‰
      leds[i] = CHSV(hue++, 255, 255); // è‰²è°ƒæ¸å˜ï¼Œå³åœ¨é¢œè‰²è½®ä¸Šç”Ÿæˆä¸€ä¸ªé²œè‰³çº¯è‰²ï¼ˆçº¢â†’æ©™â†’ç»¿â†’è“â†’ç´«â†’çº¢...ï¼‰
      FastLED.show();
      fadeall(); // æ‰€æœ‰ç¯ç ç•¥å¾®å˜æš—
      delay(10);
    }

    for (int i = NUM_LEDS - 1; i >= 0; i--) { // åå‘æ‰«æç¯ç ï¼ˆå›æ‰«ï¼‰
      leds[i] = CHSV(hue++, 255, 255);
      FastLED.show();
      fadeall();
      delay(10);
    }

    delay(100); // å¾ªç¯ä¹‹é—´çš„ç¼“å†²å»¶æ—¶
  }
}

// æ·¡å‡ºå‡½æ•°
void fadeall() {
  for (int i = 0; i < NUM_LEDS; i++) {
    leds[i].nscale8(250); // æ¸æš—
  }
}

// æš–å…‰
void warmLight() {
  fill_solid(leds, NUM_LEDS, CRGB(255, 150, 50));
  FastLED.show();
}

// å†·å…‰
void coldLight() {
  fill_solid(leds, NUM_LEDS, CRGB(150, 200, 255));
  FastLED.show();
}

// å…³é—­ç¯æ•ˆ
void turnOff() {
  fill_solid(leds, NUM_LEDS, CRGB::Black);
  FastLED.show();
}

// çˆ±å¿ƒæ•ˆæœ
void heartEffect() {
  bool on = true;
  while (true) {
    // æ£€æŸ¥æ˜¯å¦æ¥æ”¶åˆ°æŒ‡ä»¤æ¥åœæ­¢é—ªçƒ
    if (mySerial.available()) {
      String interruptCmd = mySerial.readString();
      interruptCmd.trim();

      if (interruptCmd == "close" || interruptCmd == "warm" || interruptCmd == "cold" || interruptCmd == "rainbow") {
        handleCommand(interruptCmd);  // æ‰§è¡Œå…¶ä»–æŒ‡ä»¤
        break; // è·³å‡ºçˆ±å¿ƒæ•ˆæœï¼Œå›åˆ°ä¸» loop
      }
    }

    // å…ˆæ¸…é™¤æ‰€æœ‰ LED
    fill_solid(leds, NUM_LEDS, CRGB::Black); // æ‰€æœ‰ LED å…³é—­

    // ç»˜åˆ¶çº¢è‰²å®å¿ƒçˆ±å¿ƒ
    for (int i = 0; i < sizeof(heart) / sizeof(heart[0]); i++) {
      leds[heart[i]] = on ? CRGB::Red : CRGB::Black;
    }
    FastLED.show();
    delay(500);  // æ¯æ¬¡åˆ‡æ¢åå»¶æ—¶ï¼Œæ§åˆ¶é—ªçƒé€Ÿåº¦
    on = !on; // ç¿»è½¬ç¯å…‰çŠ¶æ€ï¼Œå®å¿ƒçº¢è‰²å’Œå…³é—­
  }
}`

