        <MetricCard title="è½¬åŒ–ç‡" value={\`\${data.conversionRate}%\`} />
        <ChartComponent data={data.chartData} />
      </div>
    </div>
  );
};

export default App;`

const dataAnalysisCode = `import pandas as pd
import numpy as np
import matplotlib.pyplot as plt
import seaborn as sns
from sklearn.preprocessing import StandardScaler
from sklearn.cluster import KMeans
from sklearn.decomposition import PCA
import plotly.express as px

# æ•°æ®åŠ è½½å’Œé¢„å¤„ç†
def load_and_preprocess_data(file_path):
    # è¯»å–æ•°æ®
    df = pd.read_csv(file_path)

    # æ•°æ®æ¸…ç†
    df = df.dropna()
    df = df.drop_duplicates()

    # ç‰¹å¾å·¥ç¨‹
    df['date'] = pd.to_datetime(df['date'])
    df['month'] = df['date'].dt.month
    df['quarter'] = df['date'].dt.quarter
    df['weekday'] = df['date'].dt.weekday

    return df

# èšç±»åˆ†æ
def perform_clustering(df, features, n_clusters=5):
    # æ ‡å‡†åŒ–æ•°æ®
    scaler = StandardScaler()
    scaled_features = scaler.fit_transform(df[features])

    # K-meansèšç±»
    kmeans = KMeans(n_clusters=n_clusters, random_state=42)
    clusters = kmeans.fit_predict(scaled_features)

    df['cluster'] = clusters

    # PCAé™ç»´å¯è§†åŒ–
    pca = PCA(n_components=2)
    pca_result = pca.fit_transform(scaled_features)

    # åˆ›å»ºå¯è§†åŒ–
    fig = px.scatter(
        x=pca_result[:, 0],
        y=pca_result[:, 1],
        color=clusters,
        title='å®¢æˆ·åˆ†ç¾¤åˆ†æç»“æœ',
        labels={'x': 'PC1', 'y': 'PC2'}
    )

    return df, fig

# æ—¶é—´åºåˆ—åˆ†æ
def time_series_analysis(df, date_col, value_col):
    # æŒ‰æ—¥æœŸèšåˆ
    daily_data = df.groupby(date_col)[value_col].sum().reset_index()

    # è®¡ç®—ç§»åŠ¨å¹³å‡
    daily_data['MA_7'] = daily_data[value_col].rolling(window=7).mean()
    daily_data['MA_30'] = daily_data[value_col].rolling(window=30).mean()

    # å¯è§†åŒ–
    plt.figure(figsize=(12, 6))
    plt.plot(daily_data[date_col], daily_data[value_col], label='åŸå§‹æ•°æ®', alpha=0.7)
    plt.plot(daily_data[date_col], daily_data['MA_7'], label='7æ—¥ç§»åŠ¨å¹³å‡', linewidth=2)
    plt.plot(daily_data[date_col], daily_data['MA_30'], label='30æ—¥ç§»åŠ¨å¹³å‡', linewidth=2)
    plt.title('æ—¶é—´åºåˆ—è¶‹åŠ¿åˆ†æ')
    plt.xlabel('æ—¥æœŸ')
    plt.ylabel('æ•°å€¼')
    plt.legend()
    plt.xticks(rotation=45)
    plt.tight_layout()
    plt.show()

    return daily_data

# ç»Ÿè®¡åˆ†ææŠ¥å‘Š
def generate_statistical_report(df):
    report = {
        'basic_stats': df.describe(),
        'correlation_matrix': df.corr(),
        'missing_values': df.isnull().sum(),
        'data_types': df.dtypes
    }

    # ç›¸å…³æ€§çƒ­åŠ›å›¾
    plt.figure(figsize=(10, 8))
    sns.heatmap(report['correlation_matrix'], annot=True, cmap='coolwarm', center=0)
    plt.title('ç‰¹å¾ç›¸å…³æ€§çƒ­åŠ›å›¾')
    plt.tight_layout()
    plt.show()

    return report`

// è·å–åˆ†ç±»åç§°
const getCategoryName = () => {
