// è§¦å‘é¡¹ç›®å¡ç‰‡åŠ¨ç”»
const triggerCardAnimations = () => {
  nextTick(() => {
    const cards = document.querySelectorAll('.project-card')
    if (cards.length === 0) return

    cards.forEach((card, index) => {
      // å…ˆç¡®ä¿æ²¡æœ‰åŠ¨ç”»ç±»
      card.classList.remove('animate-in')
      // å»¶è¿Ÿæ·»åŠ åŠ¨ç”»ç±»
      setTimeout(() => {
        if (card.isConnected) { // ç¡®ä¿å…ƒç´ è¿˜åœ¨DOMä¸­
          card.classList.add('animate-in')
        }
      }, index * 100)
    })
  })
}

// å¹³æ»‘æ»šåŠ¨åˆ°æŒ‡å®šåŒºåŸŸ
const scrollToSection = (sectionId, event) => {
  event.preventDefault()

  const targetElement = document.getElementById(sectionId)
  if (targetElement) {
    // æ›´æ–°å½“å‰é€‰ä¸­çš„èŠ‚
    currentSection.value = sectionId

    // ä½¿ç”¨scrollIntoViewï¼Œä½†æ·»åŠ é€‚å½“çš„åç§»
    const yOffset = -100 // åç§»é‡ï¼Œç¡®ä¿ä¸è¢«å¯¼èˆªæ é®æŒ¡
    const y = targetElement.getBoundingClientRect().top + window.pageYOffset + yOffset

    // å¹³æ»‘æ»šåŠ¨åˆ°ç›®æ ‡ä½ç½®
    window.scrollTo({
      top: y,
      behavior: 'smooth'
    })

    // ç«‹å³ç§»é™¤å…¶ä»–é«˜äº®
    document.querySelectorAll('.section-highlight').forEach(el => {
      el.classList.remove('section-highlight')
    })

    // å»¶è¿Ÿæ·»åŠ é«˜äº®æ•ˆæœï¼Œç¡®ä¿æ»šåŠ¨å¼€å§‹åå†é«˜äº®
    setTimeout(() => {
      targetElement.classList.add('section-highlight')
      // 2ç§’åç§»é™¤é«˜äº®
      setTimeout(() => {
        targetElement.classList.remove('section-highlight')
      }, 2000)
    }, 300)
  }
}

// è¿”å›é¡¹ç›®æ¦‚è§ˆçš„ç»Ÿä¸€æ–¹æ³•
const goBackToOverview = async () => {
  selectedProject.value = null
  selectedCategory.value = 'all' // é‡ç½®åˆ†ç±»é€‰æ‹©
  currentSection.value = '' // é‡ç½®å½“å‰èŠ‚

  // ç­‰å¾…DOMæ›´æ–°åé‡æ–°è§¦å‘åŠ¨ç”»
  await nextTick()

  // æ»šåŠ¨åˆ°é¡¶éƒ¨
  window.scrollTo({ top: 0, behavior: 'smooth' })

  // çŸ­æš‚å»¶è¿Ÿåé‡æ–°è§¦å‘å¡ç‰‡åŠ¨ç”»
  setTimeout(() => {
